.menu-container {
  // width: 92%;
  display: flex;
  justify-content: flex-end;
  margin-right: 1%;
  margin: 0% 2% 0.5%;
  // padding: 1% 2%;

}
.menu-item {
  padding: 1% 2%;
    background: rgba(255,255,255,0.7);
    font-weight: bold;
    font-size: 0.9em;
    margin-left: 0.5%;
    text-decoration: none;
    color: #00497c;
    display: flex;
    align-items: center;
    margin-left: 0.5%;
    cursor: pointer;
    img {
      color: #00497c;
      width: 12px;
      margin-right: 5px;
    }
    a {
      color: #00497c;
      text-decoration: none;
    }
}