.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  color: #00497c;
}
.info-container {
  width: 92%;
  display: flex;
  background: rgba(255,255,255,0.7);
  padding: 1% 2%;
  margin: 0% 2% 0.5%;
  .info-content {
    display: inline-block;
    float: left;
    font-size: 12px;
    font-weight: normal;
    line-height: 1.1em;
    margin: 0.3% 0 0 1.5%;
    a {
      text-decoration: none;
      color: rgb(0, 0, 238);
    }
  }
}