.primary-btn {
  background: #ff7900;
  border: 1px solid #ff7100;
  color: #fff;
  font-size: 0.95em;
  height: 30px;
  margin: 0 32%;
  padding: 0 10%;
  vertical-align: middle;
  cursor: pointer;
}

.section-container {
  width: 50%;
  display: flex;
  justify-content: center;
  margin-bottom: 200px;
}
#outer {
  position: relative;
}

.bold {
  font-weight: bold;
}

.spinner-container {
  width: 100%;
    display: flex;
    justify-content: center;
    height: 100%;
    align-items: center;
    color: #fff;
}

.error-container {
     background: #f9e5e6;
    border: 1px solid #dec7cb;
    margin: 1% 0 5%;
    padding: 1.5% 3%;
    position: relative;
    width: 92%;
    display: flex;
    align-items: center;
  .error-icon {
    margin-right: 5px;
    height: 25px;
  }
  .error-message {
    font-size: 11px;
    color: #6d1d20;
  }
}
.keyboard {
  cursor: pointer;
}

.main-link-container {
  display: flex;
  align-items: center;
  position: absolute;
  right: 10px;
  top: 10px;
  a {
    color: #ec1c24;
    font-size: 12px;
  }
}

.title-container {
  margin-bottom: 200px;
  max-width: 43%;
  h1 {
    font-weight: bold;
  }
}