.form-container {
  background-color: #fff;
  width: 230px;
  border: 1px solid #e1e3e6;
  // box-shadow: 0 0 10px rgba(113, 113, 113, 0.2) inset;
  padding: 10px 20px;
  .logo-container {
    margin: 10% auto 5%;
    img {
      width: 100%;
    }
  }
  input {
    border: 1px solid #b9c3cd;
    box-shadow: 0 0 7px rgba(220, 220, 220, 0.2) inset;
    color: #969393;
    padding: 4% 3%;
    width: 92%;
    margin-bottom: 3%;
    &::placeholder { 
      font-style: italic;
    }
  }
  .reset-container {
    display: flex;
    justify-content: space-between;
    width: 90%;
    padding: 0 5%;
    align-items: center;
    margin: 0 auto 15%;
    a {
      font-size: 0.77em;
      color: #969393;
      text-decoration: underline;
      cursor: pointer;
    }
  }
  .select-language-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 15%;
    img {
      cursor: pointer;
    }
  }
}
