.content-valid-container {
  padding: 0;
    background: url(../media/gradient_bg.png) repeat-x;
    font-family: Arial,sans-serif;
    font-size: 12px;
    line-height: 16px;
    color: #000;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    .spinner-container {
      color: #00497c;
    }

}
.valid-container {
  width: 700px;
  margin-top: 20px;
  .valid-title-container {
    padding: 5px 10px 5px 9px;
    background: #3c638c url(../media/boxheader_bg.png) repeat-x 0 0;
    border: none;
    font-size: 14px;
    color: #fff;
    font-weight: bold;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    h2 {
      margin: 0 0;
      font-size: 14px;
    }
  }
  .valid-content-container {
    padding: 15px 30px 40px 30px;
    overflow: hidden;
    background-color: #fff;
    border-bottom: 1px solid #ccd7e1;
    border-left: 1px solid #ccd7e1;
    border-right: 1px solid #ccd7e1;

  }
  .content-description {
    font-size: 12px;
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
  .content-description-info {
    font-size: 12px;
    width: 100%;
    text-align: left;
    border-bottom: 1px solid #ccd7e1;
    padding: 15px 0;
  }
  .form-field {
    margin-top: 30px;
    label {
      width: 99px;
      font-size: 12px;
      margin-right: 15px;
    }
    input {
      width: 240px;
      padding: 0px !important;
      margin: 0px !important;
    }
  }
  .buttons-container {
    margin-left: 115px;
    margin-top: 10px;
    button {
      padding: 0 9px;
      background: url(../media/button_bg.png) repeat-x 0px -191px;
      color: #fff;
      height: 24px;
      font-family: inherit;
      font-weight: bold;
      cursor: pointer;
      font-size: 1em;
      -moz-border-radius: 4px;
      -khtml-border-radius: 4px;
      -webkit-border-radius: 4px;
      border-radius: 4px;
      border: 0px;
      margin-right: 15px;
    }
  }

}

.steps-container {
  display: flex;
  background-color: #f7f8fc;
    width: 100%;
    padding: 20px 0px 40px 0px;
    justify-content: space-between;

    .step-item {
      text-align: center;
      width: 33%;
      .step-item-content {
        display: flex;
        justify-content: center;
        margin-top: 20px;
      }
    }
}